:root {
    --accents-amethyst: #FFFF00;
    --accents-dodgerblue: #BFBFBF;
    --accents-turquoise: #008000;
    --bg-default: #485365;
    --code-background: #576273;
    --code-text: #FFA500;
    --ft: 'BitPotionExt';
    --ft-mono: 'BitPotionExt';
    --headings: #000000;
    --highlights-crimson: #FF4500;
    --highlights-pumpkin: #FFA500;
    --line-height-base: 1.5;
    --links-default: #FFA500;
    --links-hover: #FF0000;
    --links-visited: #FFFF00;
    --text-default: #00FFFF;
    --bg-muted: #008000;
    --bd-muted: #FFFF00;
    --bg-accent: #008000;
    --bg-subtle: #800080;
    --bd-radius: 4px;
    --border-radius: 3px;
    --table-header-bg: #008000;
    --table-header-text: #FFFF00;
    --table-border: #FFFF00;
    --btn-bg: #0000FF;
    --btn-text: #FFFF00;
    --btn-hover-bg: #FF0000;
    --btn-hover-text: #FFFF00;
    --heading-color-1: #434300;
    --heading-color-2: #004343;
    --heading-color-3: #430043;
}

html {
    box-sizing: border-box;
    margin: auto 0 auto 0;
}
*, *::before, *::after {
    box-sizing: inherit;
}
*[class*="logo" i] svg, img[src*="logo" i] {
    max-height: 70px;
    vertical-align: middle;
}
*[class*="social"] svg, *[class*="icon"] svg, svg[class*="icon"], img[class*="emoji"][src$=".svg"] {
    max-height: 30px;
    vertical-align: middle;
}
.comment-box {
    background-color: rgba(var(--accents-dodgerblue), 0.1);
    border: 1px dotted var(--accents-dodgerblue);
    border-radius: 0.1875rem;
    line-height: 1.25;
    margin: auto 0 auto 0;
    padding: 0.3125rem 1.25rem;
}
.header-container {
    align-items: center;
    display: inline-flex;
    justify-content: center;
    padding: 1rem;
    text-align: center;
}
.header-container .search-link {
    margin-top: 10px;
}
.header-container .search-link a {
    color: inherit;
    text-decoration: none;
}
.header-content {
    align-items: center;
    display: flex;
    gap: 10px;
}
.iframe-container {
    overflow: hidden;
    padding-top: 56.25%;
    position: relative;
    width: 100%;
}
.iframe-container iframe {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}
.cluster {
    rect {
        fill: var(--bg-muted);
        rx: 5px;
        ry: 5px;
        stroke: var(--bd-muted);
        stroke-width: 1px;
    }
    text {
        fill: var(--text-default);
        font-weight: 600;
    }
}
@media (prefers-color-scheme: dark) {
    .cluster rect {
        fill: var(--bg-muted);
        stroke: var(--bd-muted);
    }
    .edgeLabel {
        background-color: var(--bg-subtle);
    }
    .edgePath .path, .edgePath .arrowheadPath {
        stroke: var(--text-default);
    }
    .node rect, .node circle, .node ellipse, .node polygon {
        fill: var(--bg-accent);
        stroke: var(--text-default);
    }
    .node text, .edgeLabel, .cluster text {
        color: var(--text-default);
        fill: var(--text-default);
    }
}
.edgeLabel {
    background-color: var(--bg-subtle);
    border-radius: var(--bd-radius);
    color: var(--text-default);
    max-width: 100%;
    padding: 0.25em 0.5em;
    white-space: pre-wrap;
}
.label text {
    max-width: 100%;
    text-align: center;
    word-wrap: break-word;
}
.node {
    rect, circle, ellipse, polygon {
        fill: var(--bg-accent);
        stroke: var(--text-default);
        stroke-width: 1px;
    }
    text {
        fill: var(--text-default);
    }
}
@media print {
    .cluster rect {
        fill: #ffffff;
        stroke: #000000;
    }
    .cluster text, .node text {
        fill: #000000;
    }
    .edgeLabel {
        background-color: #ffffff;
        border: 1px solid #000000;
        color: #000000;
    }
    .edgePath .path, .edgePath .arrowheadPath {
        stroke: #000000;
    }
    .node rect, .node circle, .node ellipse, .node polygon {
        fill: #ffffff;
        stroke: #000000;
    }
}
.post-content-body {
    background-color: var(--bg-default);
    color: var(--text-default);
    font-family: var(--ft);
    line-height: var(--line-height-base);
    margin: auto 0 auto 0;
    max-width: 100vw;
    padding: 1em;
    text-align: left;
}
.post-heading .info {
    color: rgba(var(--text-default), 0.8);
}
.post-heading .tags {
    color: var(--accents-amethyst);
}
.post-heading a {
    color: var(--accents-dodgerblue);
    text-decoration: none;
}
.post-heading a:hover {
    text-decoration: underline;
}
.post-title {
    color: var(--links-default);
}
.post-wrapper {
    overflow-wrap: break-word;
    word-wrap: break-word;
}
.post-wrapper > * {
    margin: 0.5em;
}
.search-link {
    background-color: rgba(var(--accents-dodgerblue), 0.1);
    border: 1px dotted var(--accents-dodgerblue);
    border-radius: var(--bd-radius);
    display: inline-block;
    padding: 0.5em;
    transition: all 0.3s ease;
}
.search-link:hover, .search-link:focus {
    background-color: rgba(var(--accents-dodgerblue), 0.2);
    transform: scale(1.05);
}
.tagged-posts {
    li {
        margin-bottom: 0.5em;
    }
    time {
        color: rgba(var(--text-default), 0.7);
    }
    ul {
        list-style: none;
        padding-left: 0;
    }
}
.tag-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    padding: 0;
    li {
        margin: 0.25em;
    }
    a {
        background-color: rgba(var(--accents-dodgerblue), 0.1);
        border: 1px dotted var(--accents-dodgerblue);
        border-radius: var(--bd-radius);
        display: inline-block;
        padding: 0.5em;
        transition: all 0.3s ease;
        &:hover, &:focus {
            background-color: rgba(var(--accents-dodgerblue), 0.2);
            color: var(--links-visited);
            transform: scale(1.05);
        }
    }
}
.tag-page {
    margin: 1em auto;
    max-width: 100%;
    padding: 1em;
}

@mixin button-styles {
  background-color: var(--btn-bg);
  border-color: var(--btn-bg);
  color: var(--btn-text);
  padding: 0.3em 0.6em;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;

  &:hover,
  &:focus {
    background-color: var(--btn-hover-bg);
    color: var(--btn-hover-text);
  }
}

// Unique Styles

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
}

h1 {
  color: var(--heading-color-1);
}
h2 {
  color: var(--heading-color-2);
}
h3 {
  color: var(--heading-color-3);
}

p.description {
  font-weight: bold;

  &:first-letter {
    font-size: 2em;
    text-transform: uppercase;
    margin-top: -0.25em;
    float: left;
    color: var(--highlights-crimson);
    margin-right: 0.75ex;
    font-family: "Monotype Corsiva", cursive;
    font-weight: bold;
  }
}

a.bc {
  letter-spacing: 0.15ex;
}

div.flink a {
  padding: 0 0.3ex;
  line-height: 2;
}

.btn-primary,
.btn-secondary,
.convert-btn,
.action-btn {
  @include button-styles;
}

.convert-btn {
  height: 40px;
  margin: 5px 0;
  width: 100%;
}

.action-btn {
  height: 30px;
  margin: 5px 0;
}

.editor-container {
  position: fixed;
  width: 100vw;
  height: 90vh;
  display: flex;
  align-items: center;
}

.editor-ui {
  width: 50%;
  height: 100%;
  margin: 0 30px;
}

.content {
  max-width: 40em;
  margin: 0 auto;
  border: {
    left: 0.25em outset var(--bd-muted);
    right: 0.25em outset var(--bd-muted);
  }
  padding: 0.1em 5% 1.5em;
  background-color: var(--bg-default);

  pre {
    white-space: pre-wrap;
    word-wrap: break-word;
    overflow: auto;
    background-color: var(--code-background);
    color: var(--code-text);
    padding: 1em;
    border-radius: var(--border-radius);
  }
}

textarea {
  width: 100%;
  height: 90%;
  min-height: 300px;
  resize: vertical;
  background-color: var(--bg-subtle);
  color: var(--text-default);
  border: 1px solid var(--bd-muted);
  border-radius: var(--border-radius);
  padding: 0.5em;
}

.result-textarea {
  margin-top: 20px;
}

.matched-text {
  background-color: var(--highlights-pumpkin);
  color: var(--text-default);
}

#tagline {
  font-size: 0.8em;
  border-bottom: 1px solid var(--bd-muted);
  letter-spacing: 1px;
  color: var(--text-default);
}

.mermaid {
  display: block; 
  border: 1px dotted var(--accents-amethyst);
  padding: 0.5em 1em; 
}

/* Snackbar Styles*/ 
snackbar { 
  visibility: hidden;
  min-width: 250px; 
  margin-left: -125px;
  background-color: #333;
  color: #fff;
  text-align: center; 
  border-radius: 2px;
  padding: 16px;
  position: fixed; 
  left: 50%; 
  bottom: 30px; 
  font-size: 17px; 
  z-index: 1000; 

  &.show {
    visibility: visible; 
    -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
    animation: fadein 0.5s, fadeout 0.5s 2.5s;
  }
}

@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;} 
  to {bottom: 30px; opacity: 1;}
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {bottom: 30px; opacity: 1;} 
  to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}


.code-container { 
  margin-bottom: 1em; 
  position: relative;

  pre {
    margin-bottom: 0;
    border: 1px dotted var(--accents-amethyst); 
  }

  .copy-button {
    background-color: var(--btn-bg);
    border: none;
    color: var(--btn-text); 
    cursor: pointer; 
    padding: 0.5em 1em; 
    position: absolute;
    right: 5px;
    top: 5px;
    transition: background-color 0.3s ease;
    z-index: 1; 

    &:hover,
    &.copied {
      background-color: var(--btn-hover-bg);
    }

    &.copied {
      color: var(--bg-default);
    }
  }
}

/* Code highlighting styles */ 
code[class*="language-"],
pre[class*="language-"] {
  color: white;
  background: none;
  font-family: consolas, monaco, 'andale mono', 'ubuntu mono', monospace;
  font-size: 1em; 
  text-align: left; 
  white-space: pre;
  word-spacing: normal; 
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4; 
  -o-tab-size: 4; 
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none; 
  -ms-hyphens: none;
  hyphens: none;

  &.language-markup .token.tag,
  &.language-markup .token.attr-name,
  &.language-markup .token.punctuation {
    color: hsl(33, 33%, 52%); 
  }

  /* Styles specifically for code blocks (in <pre> tags) */
  &[class*="language-"] { 
    background: hsl(0, 0%, 8%);
    border-radius: .5em;
    border: .3em solid hsl(0, 0%, 33%);
    box-shadow: 1px 1px .5em black inset; 
    margin: .5em 0;
    overflow: auto;
    padding: 1em; 

    &::-moz-selection { background: hsl(200, 4%, 16%); }
    &::selection { background: hsl(200, 4%, 16%); }

    .line-highlight {
      background: hsla(0, 0%, 33%, 0.25);
      background: linear-gradient(to right, hsla(0, 0%, 33%, .1) 70%, hsla(0, 0%, 33%, 0)); 
      border-bottom: 1px dashed hsl(0, 0%, 33%);
      border-top: 1px dashed hsl(0, 0%, 33%); 
      margin-top: 0.75em; 
      z-index: 0;

      &:before,
      &[data-end]:after {
        background-color: hsl(215, 15%, 59%);
        color: hsl(24, 20%, 95%); 
      }
    }

    &.line-numbers {
      position: relative; 
      padding-left: 3.8em;
      counter-reset: linenumber; 

      > code { white-space: inherit; }

      .line-numbers-rows {
        position: absolute;
        pointer-events: none; 
        top: 0;
        font-size: 100%;
        left: -3.8em;
        width: 3em; /* works for line-numbers below 1000 lines */
        letter-spacing: -1px;
        border-right: 1px solid #999;

        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;

        > span {
          pointer-events: none;
          display: block;
          counter-increment: linenumber;

          &:before {
            content: counter(linenumber);
            color: #999;
            display: block;
            padding-right: 0.8em;
            text-align: right;
          }
        }
      }
    }
  } 

  /* Styles for inline code (in <code> tags not within <pre> tags) */
  :not(pre) > & { 
    border-radius: .3em;
    border: .13em solid hsl(0, 0%, 33%);
    box-shadow: 1px 1px .3em -.1em black inset; 
    padding: .15em .2em .05em;
    white-space: normal;
  }

  &::-moz-selection,
  & ::-moz-selection { text-shadow: none; background: hsla(0, 0%, 93%, 0.15); }
  &::selection,
  & ::selection { text-shadow: none; background: hsla(0, 0%, 93%, 0.15); }


  .token {
    &.comment,
    &.prolog, 
    &.doctype, 
    &.cdata { color: hsl(0, 0%, 47%); }
    &.punctuation { color: #999; }
    &.namespace { opacity: .7; }
    &.tag,
    &.boolean,
    &.number,
    &.constant,
    &.symbol,
    &.deleted { color: #e2777a; }
    &.selector,
    &.attr-name,
    &.string, 
    &.char, 
    &.builtin, 
    &.inserted { color: #f18eb6; } 
    &.operator,
    &.entity, 
    &.url, 
    &.variable { color: #c6a0f6; }
    &.atrule,
    &.attr-value, 
    &.keyword { color: #ef9f76; }
    &.function { color: hsl(229, 100%, 61%); }
    &.regex, 
    &.important { color: #e90; }
    &.important, 
    &.bold { font-weight: bold; } 
    &.italic { font-style: italic; }
    &.entity { cursor: help; }
    &.property, 
    &.tag,
    &.constant,
    &.symbol,
    &.number { color: hsl(352, 100%, 69%); }

    &.atrule {
      color: hsl(352, 100%, 69%); 
    } 

    &.attr-value {
      color: hsl(75, 70%, 60%); 
    }

    &.keyword {
      color: hsl(39, 100%, 56%);
    }

    &.selector {
      color: hsl(75, 70%, 60%);
    } 

    &.operator {
      color: hsl(39, 100%, 56%); 
    } 

    &.function,
    &.deleted {
      color: hsl(0, 100%, 67%);
    }

    &.boolean {
      color: hsl(0, 100%, 67%); 
    }

    &.variable {
      color: hsl(0, 100%, 67%);
    }

    /* Ensure tokens are on top of their background. */
    position: relative;
    z-index: 1;
  }
}
